  height: min(15rem, 60vw);
  width: 100%;
  background-image: var(--mobile-illustration1), var(--mobile-illustration2);
  background-repeat: no-repeat;
  background-size: auto 85%, auto 50%;
  background-position: 50% 0%, 50% 100%;

  @media (min-width: 57.06rem) {
    grid-row: 1;
    grid-column: 1;
    height: 100%;
    background-color: hsl(0, 0%, 100%);
    border-radius: 1.25rem 0 0 1.25rem;
    background-image: var(--desktop-illustration1), var(--desktop-illustration2);
    background-size: 120%, auto;
    background-position: 100% 50%, 100% 60%;

    &::before {
      content: "";
      display: block;
      height: 100%;
      width: 50%;
      background-image: var(--desktop-illustration3);
      background-repeat: no-repeat;
      background-size: 100%;
      background-position: left 65%;
      position: relative;
      left: -24.5%;
    }
  }
}

.faq {
  grid-row: 2 / span 2;
  grid-column: 1;
  padding: min(7.5rem, 30vw) 1.5rem 2.5rem;
  background-color: hsl(0, 0%, 100%);
  border-radius: 1.25rem;
  box-shadow: 0 0.62rem 1.87rem hsla(0, 0%, 0%, 0.2);

  @media (min-width: 57.06rem) {
    grid-row: 1;
    grid-column: 2;
    padding: 3.5rem 4rem 3.5rem;
    border-radius: 0 1.25rem 1.25rem 0;
  }

  .answer {
    display: none;
    padding: 10px;
  }

  .answer.visible {
    display: block;
  }

  h2 {
    color: var(--very-dark-blue);
    font-weight: 700;
    font-size: 2.5rem;
    text-align: center;
    margin: 1.2em 0;

    @media (min-width: 57.06rem) {
      text-align: left;
      margin: 0 0 1.65em 0;
    }
  }

  .q-a {
    font-size: 0.95rem;
    border-bottom: 1.5px solid var(--light-grayish-blue);
    padding-bottom: 1.05em;
    margin-top: 1.05em;
  }

  .q-wrapper {
    display: grid;
    grid-template-columns: 1fr auto;
  }

  h3 {
    grid-column: 1;
    color: var(--very-dark-grayish-blue);
    cursor: pointer;
    font-size: 1.4rem;

    &:hover {
      color: var(--yellow-color);
    }

    &.h3-active {
      color: var(--very-dark-blue);
      font-weight: 700;
    }
  }

  svg {
    grid-column: 2;
    padding: 0.5rem 0 0 0.7rem;
    justify-self: end;
    cursor: pointer;
    transition: transform 0.2s;

    &.svg-animation {
      transform: rotate(180deg);
      padding: 0 0.7rem 0.5rem 0;
    }
  }

  p {
    width: calc(100% - calc(10px + 0.7rem));
    color: var(--custom-color);
    margin-top: 1.05em;
    font-size: 1.1rem;

    &.p-visible {
      display: block;
    }
  }
}
